		loginClose
	} from '@/api/login.js'
	export default {
		data() {
			return {
				userInfo: {},
				bindCardUrl: '',
				platform: ''
			}
		},
		computed: {
			avatar() {
				return this.$store.state.user.avatar
			},
			windowHeight() {
				return uni.getSystemInfoSync().windowHeight - 50
			},
			statusBarHeight() {
				return uni.getSystemInfoSync().statusBarHeight
			},


		},
		onLoad() {
			console.log(uni.getSystemInfoSync())
			this.getUserInfo()
			// #ifdef APP-PLUS
			this.platform = uni.getSystemInfoSync().platform;
			console.log(platform)
			// #endif
		},
		onShow() {

		},
		methods: {
			getUserInfo() {
				getMemberCenter().then((res) => {
					this.userInfo = res.myinfo
				})
			},
			navTo(url) {
				uni.navigateTo({
					url: url
				})
			},
			doLogout() {

				logout().then((res) => {
					this.$store.dispatch('LogOut')
					uni.reLaunch({
						url: "/pages/login"
					})
				})
			},
			doLoginClose() {
				uni.showModal({
					title:'确定注销？',
					content: '确定要注销吗？注销成功后，帐号将被收回无法登录！',
					confirmText: '确定',
					cancelText:'取消',
					success: (res) => {
						if(res.confirm) {
							loginClose().then((res) => {
								uni.reLaunch({
									url:'/pages/login'
								})
							})
						}
					}
				})
			}
		}
	}
</script>
